export default "window.alert = function () {\n  var nodeId = '%NODEID%';\n  return function (msg) {\n    var loadingErrRegExp = new this.RegExp(/error/gmi);\n\n    var postResponse = function postResponse(message, level) {\n      var resp = {\n        nodeId: nodeId,\n        type: 'alert',\n        message: message,\n        level: level\n      };\n      parent.postMessage(resp, window.origin);\n    };\n\n    if (loadingErrRegExp.test(msg)) {\n      postResponse(msg, 'error');\n    } else {\n      postResponse(msg, 'info');\n    }\n  };\n}();"